/* eslint-disable camelcase */
import { DEFAULT_AGGREGATION_RES_LEVEL_QUADBIN } from "../constants.js";
import { baseSource } from "./base-source.js";
export const quadbinTableSource = async function (options) {
    const { aggregationExp, aggregationResLevel = DEFAULT_AGGREGATION_RES_LEVEL_QUADBIN, spatialDataColumn = 'quadbin', tableName } = options;
    const urlParameters = {
        aggregationExp,
        name: tableName,
        spatialDataColumn,
        spatialDataType: 'quadbin'
    };
    if (aggregationResLevel) {
        urlParameters.aggregationResLevel = String(aggregationResLevel);
    }
    return baseSource('table', options, urlParameters);
};
